    import service from '../../service.js'
	import ButtonItems from '../../components/ButtonItems.vue'
    import mInput from '../../components/m-input.vue'
     
    export default {
        components: {
            mInput,
			ButtonItems,
        },
        data() {
            return {
                providerList: [],
                hasProvider: false,
                userName: '',
                password: '',
                positionTop: 0,
				loginData:{
					grant_type: 'password',
					client_id: 'wanxin-p2p-web-h5',
					client_secret: 'itcasth5',
					domain: 'c',
					authenticationType: 'password'
				}
            }
        },
        methods: {
			setusername(event){
				this.userName = event
			},
			setPassword(event){
				this.password = event
			},
            login() {
                if (this.userName.length != 11) {
                    uni.showToast({
                        icon: 'none',
                        title: '请输入正确的手机号！'
                    });
                    return;
                }
                if (this.password.length < 6) {
                    uni.showToast({
                        icon: 'none',
                        title: '密码最短为 6 个字符'
                    });
                    return;
                }
                const data = {
                    username: this.userName,
                    password: this.password,
					mobile:this.userName,
					...this.loginData
                };
				this.request({
					url: `consumer/check-username/${this.userName}`
				}).then(res => {
					if(res.data.result == 1){
						this.log(data)
					} else {
						this.reg(data)
					}
				}).catch(err => {
					uni.showToast({
						icon: 'none',
						title: '手机号识别失败，请稍后重试！',
					});
				})
            },
			reg(data){
				this.request({
						url: 'consumer/consumers',
						params: {...data, role: ''},
						method: 'POST'
					}).then(res => {
						this.log(data)
					}).catch(err => {
						uni.showToast({
							icon: 'none',
							title: '注册失败，请稍后重试！',
						});
					})
			},
			log(data){
				this.request({
					url: 'uaa/oauth/token',
					params: data,
					method: 'POST'
				}).then(res => {
					uni.setStorage({key:'loginData', data: data})
					uni.setStorage({key:'access_token', data: res.data.access_token})
					uni.switchTab({url: '/pages/main/main'});
				}).catch(err => {
					uni.showToast({
						icon: 'none',
						title: '登录失败，请稍后重试！'
					});
				})
			},
			getCode(){
				if(this.userName.length == 11){
					this.password = '123456'
				}else{
					uni.showToast({
                        icon: 'none',
                        title: '请输入正确的手机号！'
                    });
                    return;
				}
			}
        }
    }
